* {
  padding: 0;
  margin: 0;
  outline: none;
}
*:focus,
*:active {
  outline: none;
}
*::-moz-focus-outer {
  border: 0;
}

input:focus {
  outline: none;
}

body {
  font-family: "MuseoModerno", cursive;
  color: white;

  background: fixed no-repeat center;
  background-size: cover;
  -webkit-backdrop-filter: brightness(0.7);
          backdrop-filter: brightness(0.7);
  -webkit-animation: background-change 300s 0s infinite;
          animation: background-change 300s 0s infinite;
}

div.main-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  min-height: 85vh;
  height: 100%;
}

button {
  background: none;
  border: none;
}

button:hover {
  cursor: pointer;
}

header.header {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 15vh;

  -webkit-backdrop-filter: blur(2px);

          backdrop-filter: blur(2px);
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.089);
}

header.header h1 a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
}

div.audios {
  display: grid;
  grid-template-columns: repeat(5, 1fr);

  grid-gap: 30px;

  gap: 30px;
}

@media (max-width: 650px) {
  div.audios {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 550px) {
  div.audios {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
@media (max-width: 400px) {
  div.audios {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 300px) {
  div.audios {
    grid-template-columns: 1fr;
  }
}

div.sound-component {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100px;
  height: 100px;
}

div.sound-button {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100px;
  height: 100px;

  opacity: 0.5;

  transition: 0.1s;
}
div.sound-button:hover {
  cursor: pointer;
  opacity: 0.8;
}
div.sound-button.selected {
  opacity: 1;
}

div#configurations {
  display: flex;
  flex-direction: column;

  padding: 5px;
  margin-top: 50px;
}

div#configurations h4 {
  color: rgba(255, 255, 255, 0.7);
}

div#configurations div.fieldset {
  display: flex;
  justify-content: center;
  align-items: center;
}

div#configurations div.fieldset span.fieldset-title {
  border: none;
  border-radius: 10px 0 0 10px;

  padding: 10px;

  background: rgba(255, 255, 255, 0.7);

  color: rgba(0, 0, 0, 0.575);
}

div#configurations div.fieldset input#url {
  border: none;
  border-radius: 0 10px 10px 0;

  padding: 15px;

  background: rgba(255, 255, 255, 0.5);

  color: rgba(0, 0, 0, 0.753);
}

div#configurations button {
  border-radius: 10px;

  padding: 15px;
  margin-top: 5px;

  background: rgba(255, 255, 255, 0.5);
  color: rgba(0, 0, 0, 0.753);
  transition: 0.1s;
}

div#configurations button:hover {
  background-color: rgba(255, 255, 255, 0.7);
}
div#configurations button:active {
  background-color: white;
}

input[type="range"].audio-controller {
  opacity: 0;

  -webkit-appearance: none;

     -moz-appearance: none;

          appearance: none;

  width: 100%;
  height: 2px;

  background: white;

  cursor: pointer;
}

/* MARKER */
/* Chrome/Safari/Opera */
input[type="range"].audio-controller::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

/* Firefox */
input[type="range"].audio-controller::-moz-range-thumb {
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

/* IE */
input[type="range"].audio-controller::-ms-thumb {
  height: 13px;
  width: 13px;

  background: white;

  border: none;
  border-radius: 100%;
}

input[type="range"].audio-controller.selected {
  opacity: 1;
}

